<template>
    <div class="player">
      <div id="player"></div>
    </div>
</template>

<script>
export default {
  name:"Player",
  data () {
    return {

    }
  },
  mounted(){
      let user_token = this.$settings.check_user_login();
      let user_name = localStorage.user_name || sessionStorage.user_name;
      let self = this;
      if(!user_token){
          this.$alert("","",{
              callback(){
                self.$router.push("/login");
              }
          });
          return;
      }

      // vid
      let vid = "0411ff75f685f81f5ed1a7af11810895_0";
      var player = polyvObject('#player').videoPlayer({
        wrap: '#player',
        width: document.documentElement.clientWidth-260, // 
        height: document.documentElement.clientHeight,   // 
        forceH5: true,
        vid: vid,
        code: user_name, // 
        // 
        playsafe: function (vid, next) { // token
            self.$axios.get(`${self.$settings.Host}/courses/player/`,{
              params:{
                vid: vid,
              },
              headers:{
                "Authorization":"jwt " + user_token,
              }
            }).then(function (response) {
                console.log(response);
                next(response.data);
            })
        }
    });
  },
  methods: {

  },
  computed: {
  }
}
</script>

<style scoped>
</style>



