<template>
    <div class="cart_item">
      <div class="cart_column column_1">
        <el-checkbox class="my_el_checkbox" v-model="course.selected"></el-checkbox>
      </div>
      <div class="cart_column column_2">
        <img :src="$settings.Host+course.course_img" alt="">
        <span><router-link :to="`/courses/${course.course_id}`">{{course.course_name}}</router-link></span>
      </div>
      <div class="cart_column column_3">
        <el-select v-model="course.expire" size="mini" placeholder="" class="my_el_select">
          <el-option v-for="item in course.expire_list" :label="item.text" :value="item.time" :key="item.time"></el-option>
        </el-select>
      </div>
      <div class="cart_column column_4">{{course.price}}</div>
      <div class="cart_column column_4"><span @click="cart_delete"></span></div>
    </div>
</template>

<script>
export default {
    name: "CartItem",
    props:["course"],
    data(){
      return {
        checked: false,
      }
    },
    created(){
        this.user_token = this.$settings.check_user_login();
    },
    watch:{
        "course.selected": function(){
            // 
            this.change_selected_status();
        },
        "course.expire": function(){
          this.change_course_expire();
        },
    },
    methods:{
        change_course_expire(){
            // expire_list
            for(let item of this.course.expire_list){ // for ... of es6for ..in 
                if( this.course.expire == item.time ){
                    this.course.price = item.price;
                }
            }
            // redis
            this.$axios.put(`${this.$settings.Host}/cart/`,{
                course_id: this.course.course_id,
                time: this.course.expire
            },{
                headers:{ // apitoken
                  "Authorization": "jwt " + this.user_token,
                }
            }).then(response=>{
                // 
                this.$emit("change_course", this.course);
            }).catch(error=>{
                this.$message.error("");
            })
        },
        change_selected_status(){
            // 
            this.$axios.patch(`${this.$settings.Host}/cart/`,{
                "course_id": this.course.course_id,
	              "selected": this.course.selected,
            },{
                headers:{ // apitoken
                  "Authorization": "jwt " + this.user_token,
                }
            }).then(response=>{
                // 
                this.$emit("change_course", this.course);
            }).catch(error=>{
               this.$message.error("");
            });
        },
        cart_delete(){
           // 
           this.$confirm(`<${this.course.course_name}>`, '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }).then(() => { // 
                  this.deleteHandler();
                }).catch(() => {

                });
        },
        deleteHandler(){
            // 
            this.$axios.delete(`${this.$settings.Host}/cart/`,{
                params:{
                    course_id: this.course.course_id,
                },
                headers:{
                    "Authorization": "jwt " + this.user_token,
                }
            }).then(response=>{
                // 
                this.$emit("delete_cart");
                this.$message.success("");
            }).catch(error=>{
                this.$message.error("");
            });
        }

    }
}
</script>

<style scoped>
.cart_item::after{
  content: "";
  display: block;
  clear: both;
}
.cart_column{
  float: left;
  height: 250px;
}
.cart_item .column_1{
  width: 88px;
  position: relative;
}
.my_el_checkbox{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  width: 16px;
  height: 16px;
}
.cart_item .column_2 {
  padding: 67px 10px;
  width: 520px;
  height: 116px;
}
.cart_item .column_2 img{
  width: 175px;
  height: 115px;
  margin-right: 35px;
  vertical-align: middle;
}
.cart_item .column_3{
  width: 197px;
  position: relative;
  padding-left: 10px;
}
.my_el_select{
  width: 117px;
  height: 28px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
}
.cart_item .column_4{
  padding: 67px 10px;
  height: 116px;
  width: 142px;
  line-height: 116px;
}

</style>



